<template>
  <li :style="zoom">
    <div
      ref="storyteller"
      class="storyteller"
      @focusout="isMenuOpen = false"
      tabindex="-1"
      :class="[{ 'menu-open': isMenuOpen }, storyteller.role.team]"
    >
      <div
        class="night-order first"
        :class="{
          shown:
            nightOrder.get(storyteller).first &&
            grimoire.isNightOrder &&
            !session.reveal,
        }"
      >
        <em>{{ nightOrder.get(storyteller).first }}</em>
        <span class="description" v-if="storyteller.role.firstNightReminder">
          <span v-html="storyteller.role.firstNightReminder"></span>
        </span>
      </div>
      <div
        class="night-order other"
        :class="{
          shown:
            nightOrder.get(storyteller).other &&
            grimoire.isNightOrder &&
            !session.reveal,
        }"
      >
        <em>{{ nightOrder.get(storyteller).other }}</em>
        <span class="description" v-if="storyteller.role.otherNightReminder">
          <span v-html="storyteller.role.otherNightReminder"></span>
        </span>
      </div>

      <div class="menu-container">
        <Token
          :role="storyteller.role"
          @click-token="isMenuOpen = !isMenuOpen"
        />

        <transition name="fold">
          <ul class="menu" v-if="isMenuOpen">
            <li
              @click="
                $emit('trigger', ['openRoleModal']);
                isMenuOpen = !isMenuOpen;
              "
            >
              <font-awesome-icon icon="masks-theater" />Change Ability
            </li>
            <li
              @click="
                $emit('trigger', ['openReminderModal']);
                isMenuOpen = false;
              "
              v-if="session.isSpectator"
            >
              <font-awesome-icon icon="circle-plus" />Add Reminder
            </li>
            <li @click="changePronouns" v-if="!session.isSpectator">
              <font-awesome-icon icon="venus-mars" />Change Pronouns
            </li>
            <li @click="changeName" v-if="!session.isSpectator">
              <font-awesome-icon icon="user-pen" />Rename
            </li>
          </ul>
        </transition>
      </div>

      <div
        class="name"
        @click="isMenuOpen = !isMenuOpen"
        :class="{ active: isMenuOpen }"
      >
        <span>{{ storyteller.name }}</span>
        <span v-if="storyteller.pronouns">&emsp13;</span>
        <font-awesome-icon icon="venus-mars" v-if="storyteller.pronouns" />
        <div class="pronouns" v-if="storyteller.pronouns">
          <span>{{ storyteller.pronouns }}</span>
        </div>
      </div>
    </div>

    <template v-if="storyteller.reminders">
      <div
        class="reminder"
        :key="reminder.role + ' ' + reminder.name"
        v-for="reminder in storyteller.reminders"
        :class="{
          custom: reminder.role === 'custom' && reminder.name.length > 15,
          flipped: reminder.flipped,
        }"
      >
        <span
          class="icon"
          :style="{
            backgroundImage: `url(${
              reminder.image && grimoire.isImageOptIn
                ? reminder.image
                : require(
                    '../assets/icons/' +
                      (reminder.imageAlt || reminder.role) +
                      (reminder.team === 'townsfolk' ||
                      reminder.team === 'outsider'
                        ? '_g'
                        : reminder.team === 'minion' ||
                            reminder.team === 'demon'
                          ? '_e'
                          : '') +
                      '.webp',
                  )
            })`,
          }"
        ></span>
        <svg
          viewBox="10 10 180 180"
          class="name"
          v-if="reminder.role !== 'custom' || reminder.name.length <= 15"
        >
          <path
            d="M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0"
            id="circle"
            fill="transparent"
            transform="rotate(48, 100, 100)"
          />
          <text width="150" dx="100%" text-anchor="middle" class="text">
            <textPath xlink:href="#circle">
              {{ reminder.name }}
            </textPath>
          </text>
        </svg>
        <span class="customText" v-else>{{ reminder.name }}</span>
        <div class="overlay">
          <font-awesome-icon
            @click="flipReminder(reminder)"
            icon="rotate-right"
            class="flip"
          />
          <font-awesome-icon
            @click="removeReminder(reminder)"
            icon="trash-can"
            class="remove"
          />
        </div>
      </div>
    </template>
    <div
      class="reminder add"
      @click="$emit('trigger', ['openReminderModal'])"
      v-if="!session.isSpectator"
    >
      <span class="icon"></span>
    </div>
    <div class="reminderHoverTarget" v-if="!session.isSpectator"></div>
  </li>
</template>

<script>
import Token from "./Token";
import { mapGetters, mapState } from "vuex";

export default {
  components: {
    Token,
  },
  props: {
    storyteller: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapState("players", ["players"]),
    ...mapState(["grimoire", "session"]),
    ...mapGetters({ nightOrder: "players/nightOrder" }),
    zoom: function () {
      if (this.players.length < 7) {
        return { width: 18 + this.grimoire.zoom + "vh" };
      } else if (this.players.length <= 10) {
        return { width: 16 + this.grimoire.zoom + "vh" };
      } else {
        return { width: 14 + this.grimoire.zoom + "vh" };
      }
    },
  },
  data() {
    return {
      isMenuOpen: false,
    };
  },
  methods: {
    changePronouns() {
      if (this.session.isSpectator) return;
      const pronouns = prompt(
        "Storyteller Pronouns",
        this.storyteller.pronouns,
      );
      //Only update pronouns if not null (prompt was not cancelled)
      if (pronouns !== null) {
        this.updateStoryteller("pronouns", pronouns, true);
      }
    },
    changeName() {
      if (this.session.isSpectator) return;
      const name =
        prompt("Storyteller Name", this.storyteller.name) ||
        this.storyteller.name;
      this.updateStoryteller("name", name, true);
    },
    removeReminder(reminder) {
      const reminders = [...this.storyteller.reminders];
      reminders.splice(this.storyteller.reminders.indexOf(reminder), 1);
      this.updateStoryteller("reminders", reminders, true);
    },
    flipReminder(reminder) {
      const reminders = [...this.storyteller.reminders];
      reminder.flipped = !reminder.flipped;
      this.updateStoryteller("reminders", reminders, true);
    },
    updateStoryteller(property, value, closeMenu = false) {
      if (
        this.session.isSpectator &&
        property !== "reminders" &&
        property !== "pronouns"
      )
        return;
      this.$store.commit("players/updateStoryteller", {
        property,
        value,
      });
      if (closeMenu) {
        this.isMenuOpen = false;
      }
    },
  },
};
</script>

