<template>
  <div
    class="token"
    @click="clickToken"
    :class="[role.id ? role.id : 'blank', getEdition + '-token']"
  >
    <span
      class="icon"
      v-if="role.id"
      :style="{
        backgroundImage: `url(${
          role.image && grimoire.isImageOptIn
            ? Array.isArray(role.image)
              ? role.image[getCustomRoleIconIndex]
              : role.image
            : require(
                '../assets/icons/' +
                  (role.imageAlt || role.id) +
                  (getAlignment !== 'n' ? '_' + getAlignment : '') +
                  '.webp',
              )
        })`,
      }"
    ></span>
    <span
      v-if="role.firstNight || role.firstNightReminder"
      :class="[getEdition + '-leaf-left']"
    ></span>
    <span
      v-if="role.otherNight || role.otherNightReminder"
      :class="[getEdition + '-leaf-right']"
    ></span>
    <span
      v-if="reminderLeaves"
      :class="[getEdition + '-leaf-top' + reminderLeaves]"
    ></span>
    <span class="leaf-orange" v-if="role.setup"></span>
    <svg viewBox="0 0 150 150" class="name">
      <path
        d="M 13 75 C 13 160, 138 160, 138 75"
        id="curve"
        fill="transparent"
      />
      <text
        width="150"
        x="66.6%"
        text-anchor="middle"
        class="label mozilla"
        :font-size="role.name | nameToFontSize"
        :letter-spacing="role.name | nameToLetterSpacing"
      >
        <textPath xlink:href="#curve">
          {{ role.name }}
        </textPath>
      </text>
    </svg>
    <div class="ability" v-if="role.ability">
      {{ role.ability }}
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Token",
  props: {
    role: {
      type: Object,
      default: () => ({}),
    },
    alignment: {
      type: String,
    },
  },
  computed: {
    reminderLeaves: function () {
      return (
        (this.role.reminders || []).length +
        (this.role.remindersGlobal || []).length
      );
    },
    getAlignment: function () {
      return (
        this.alignment ||
        (this.role.team === "townsfolk" || this.role.team === "outsider"
          ? "g"
          : this.role.team === "minion" || this.role.team === "demon"
            ? "e"
            : "n")
      );
    },
    getCustomRoleIconIndex: function () {
      return !this.alignment
        ? 0
        : (this.alignment === "g" &&
              (this.role.team === "townsfolk" ||
                this.role.team === "outsider")) ||
            (this.alignment === "e" &&
              (this.role.team === "minion" || this.role.team === "demon")) ||
            this.alignment === "n"
          ? 0
          : this.alignment &&
              (this.role.team === "townsfolk" ||
                this.role.team === "outsider" ||
                this.role.team === "minion" ||
                this.role.team === "demon" ||
                (this.role.team === "traveler" && this.alignment === "g"))
            ? 1
            : 2;
    },
    getEdition: function () {
      return !this.role.id
        ? this.edition.id
        : this.role.team === "traveler" || this.role.team === "fabled"
          ? "taf"
          : this.role.edition === "tb" ||
              this.role.edition === "bmr" ||
              this.role.edition === "snv"
            ? this.role.edition
            : "custom";
    },
    ...mapState(["grimoire", "edition"]),
  },
  data() {
    return {};
  },
  filters: {
    nameToFontSize: (name) => (name && name.length > 10 ? "95%" : "110%"),
    nameToLetterSpacing: (name) => (name && name.length > 10 ? "0.5px" : "1px"),
  },
  methods: {
    clickToken() {
      this.$emit("click-token");
    },
  },
};
</script>

