<template>
  <div
    id="app"
    @keyup="keyup"
    @keyup.space="space"
    tabindex="-1"
    :class="[
      {
        night: grimoire.isNight,
        reveal: session.reveal,
        static: grimoire.isStatic,
      },
      edition.id,
      `background-${
        players.length
          ? (edition.isOfficial
              ? grimoire.defaultBaseBackgroundIndex
              : grimoire.defaultCustomBackgroundIndex) + 1
          : 0
      }`,
    ]"
    :style="{
      backgroundImage: grimoire.background
        ? `url('${grimoire.background}')`
        : '',
    }"
  >
    <video
      id="background"
      v-if="grimoire.background && grimoire.background.match(/\.(mp4|webm)$/i)"
      :src="grimoire.background"
      autoplay
      loop
    ></video>
    <div class="backdrop"></div>
    <TownSquare></TownSquare>
    <transition name="blur">
      <Intro v-if="!players.length"></Intro>
      <TownInfo
        v-if="players.length && !session.nomination && !session.reveal"
      ></TownInfo>
      <GameResult v-if="players.length && session.reveal"></GameResult>
      <Vote v-if="session.nomination" ref="vote"></Vote>
    </transition>
    <Menu ref="menu"></Menu>
    <EditionModal />
    <FabledModal />
    <ExtraRolesModal />
    <RolesModal />
    <ReferenceModal />
    <NightOrderModal />
    <VoteHistoryModal />
    <GameStateModal />
    <Gradients />
    <span id="version">v{{ version }}</span>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { version } from "../package.json";
import TownSquare from "./components/TownSquare";
import TownInfo from "./components/TownInfo";
import Menu from "./components/Menu";
import RolesModal from "./components/modals/RolesModal";
import EditionModal from "./components/modals/EditionModal";
import Intro from "./components/Intro";
import ReferenceModal from "./components/modals/ReferenceModal";
import Vote from "./components/Vote";
import Gradients from "./components/Gradients";
import NightOrderModal from "./components/modals/NightOrderModal";
import FabledModal from "@/components/modals/FabledModal";
import VoteHistoryModal from "@/components/modals/VoteHistoryModal";
import GameStateModal from "@/components/modals/GameStateModal";
import ExtraRolesModal from "@/components/modals/ExtraRolesModal.vue";
import GameResult from "@/components/GameResult.vue";

export default {
  components: {
    GameResult,
    ExtraRolesModal,
    GameStateModal,
    VoteHistoryModal,
    FabledModal,
    NightOrderModal,
    Vote,
    ReferenceModal,
    Intro,
    TownInfo,
    TownSquare,
    Menu,
    EditionModal,
    RolesModal,
    Gradients,
  },
  computed: {
    ...mapState(["edition", "grimoire", "session"]),
    ...mapState("players", ["players"]),
  },
  data() {
    return {
      version,
    };
  },
  methods: {
    keyup({ key, ctrlKey, metaKey }) {
      if (ctrlKey || metaKey) return;
      switch (key.toLocaleLowerCase()) {
        case "g":
          this.$store.commit("toggleGrimoire");
          break;
        case "f":
          this.$store.commit("toggleFabled");
          break;
        case "b":
          this.$store.commit("toggleBluffs");
          break;
        case "a":
          this.$refs.menu.addPlayer();
          break;
        case "h":
          this.$refs.menu.hostSession();
          break;
        case "j":
          this.$refs.menu.joinSession();
          break;
        case "r":
          this.$store.commit("toggleModal", "reference");
          break;
        case "n":
          this.$store.commit("toggleModal", "nightOrder");
          break;
        case "e":
          if (this.session.isSpectator || this.session.reveal) return;
          this.$store.commit("toggleModal", "edition");
          break;
        case "c":
          if (this.session.isSpectator || this.session.reveal) return;
          this.$store.commit("toggleModal", "roles");
          break;
        case "v":
          if (this.session.voteHistory.length || !this.session.isSpectator) {
            this.$store.commit("toggleModal", "voteHistory");
          }
          break;
        case "s":
          if (this.session.isSpectator) return;
          this.$refs.menu.toggleNight();
          break;
        case "escape":
          this.$store.commit("toggleModal");
          break;
        case "arrowup":
          if (this.session.nomination) {
            this.$refs.vote.vote(true);
          }
          break;
        case "arrowdown":
          if (this.session.nomination) {
            this.$refs.vote.vote(false);
          }
          break;
      }
    },
    space() {
      const index = this.players.findIndex(
        (p) => p.id === this.session.playerId,
      );
      if (index >= 0) {
        this.$store.commit("session/raiseHand", [
          index,
          !this.session.handsRaised[index],
        ]);
      }
    },
  },
};
</script>

