<template>
  <div class="center">
    <div
      class="edition"
      v-if="grimoire.defaultBaseBackgroundIndex > 4 || !edition.isOfficial"
      :class="[
        {
          custom:
            (edition.logo && grimoire.isImageOptIn) ||
            (edition.name && !edition.isOfficial),
        },
        'edition-' + edition.id,
      ]"
      :style="{
        backgroundImage: `url(${
          edition.logo && grimoire.isImageOptIn
            ? edition.logo
            : require(
                '../assets/editions/' +
                  (!edition.isOfficial && edition.name
                    ? 'custom-label'
                    : edition.id) +
                  '.webp',
              )
        })`,
      }"
    ></div>
    <span
      class="meta"
      v-if="
        !(edition.logo && grimoire.isImageOptIn) &&
        edition.name &&
        !edition.isOfficial
      "
    >
      {{ edition.name }}
      {{ edition.author ? "by " + edition.author : "" }}
    </span>

    <div class="info">
      <table>
        <tr v-if="players.length - teams.traveler < 5">
          <td colspan="4">Five or more players are required</td>
        </tr>
        <tr>
          <td>Players</td>
          <td>
            {{ players.length }}
          </td>
          <td>Alive</td>
          <td>
            {{ teams.alive }}
          </td>
        </tr>
        <tr v-if="players.length - teams.traveler >= 5">
          <td class="townsfolk">Townsfolk</td>
          <td class="townsfolk">
            {{ teams.townsfolk }}
          </td>
          <td class="demon">Minions</td>
          <td class="demon">
            {{ teams.minion }}
          </td>
        </tr>
        <tr v-if="players.length - teams.traveler >= 5">
          <td class="townsfolk">Outsiders</td>
          <td class="townsfolk">
            {{ teams.outsider }}
          </td>
          <td class="demon">Demons</td>
          <td class="demon">
            {{ teams.demon }}
          </td>
        </tr>
        <tr class="traveler" v-if="teams.traveler">
          <td colspan="3">Travellers</td>
          <td>
            {{ teams.traveler }}
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import gameJSON from "./../game";
import { mapState } from "vuex";

export default {
  computed: {
    teams: function () {
      const { players } = this.$store.state.players;
      const nonTravelers = this.$store.getters["players/nonTravelers"];
      const alive = players.filter((player) => player.isDead !== true).length;
      return {
        ...gameJSON[nonTravelers - 5],
        traveler: players.length - nonTravelers,
        alive,
        votes:
          alive +
          players.filter(
            (player) => player.isDead === true && player.isVoteless !== true,
          ).length,
      };
    },
    ...mapState(["edition", "grimoire"]),
    ...mapState("players", ["players"]),
  },
};
</script>

