<template>
  <Modal
    class="night-reference"
    :class="{
      isStoryteller: !session.isSpectator,
    }"
    @close="toggleModal('nightOrder')"
    v-if="modals.nightOrder && roles.size"
  >
    <font-awesome-icon
      @click="toggleModal('reference')"
      icon="address-card"
      class="toggle"
      title="Show Character Reference"
    />
    <h3>
      Night Order –
      {{ edition.name || "Custom Script" }}
    </h3>
    <div class="night">
      <ul class="first">
        <li class="headline">First Night</li>
        <li
          v-for="role in rolesFirstNight"
          :key="role.name"
          :class="[role.team]"
        >
          <span class="name">
            {{ role.name }}
            <span
              class="player"
              v-if="
                role.players.length ||
                storyteller.role.id === role.id ||
                extraRoles.some((extraRole) => extraRole.id === role.id)
              "
            >
              <br />
              <small>
                <template
                  v-if="
                    extraRoles.some((extraRole) => extraRole.id === role.id)
                  "
                  >{{
                    "Extra Character" +
                    (role.players.length > 0 || storyteller.role.id === role.id
                      ? ","
                      : "")
                  }}</template
                >
              </small>
              <small>
                <template v-if="storyteller.role.id === role.id">{{
                  storyteller.name + (role.players.length > 0 ? "," : "")
                }}</template>
              </small>
              <small
                v-for="(player, index) in role.players"
                :class="{ dead: player.isDead || player.isInvisibleDead }"
                :key="index"
              >
                <template v-if="!session.isSpectator">
                  <div class="option" @click="setResponded(player, role.id)">
                    {{ player.name }}
                    <font-awesome-icon
                      :icon="[
                        'fas',
                        !!player.hasResponded[role.id]
                          ? 'square-check'
                          : 'square',
                      ]"
                    />
                  </div>
                </template>
                <template v-if="session.isSpectator"
                  >{{
                    player.name + (role.players.length > index + 1 ? "," : "")
                  }}
                </template>
              </small>
            </span>
          </span>
          <span
            class="icon"
            v-if="role.id"
            :style="{
              backgroundImage: `url(${
                role.image && grimoire.isImageOptIn
                  ? Array.isArray(role.image)
                    ? role.image[0]
                    : role.image
                  : require(
                      '../../assets/icons/' +
                        (role.imageAlt || role.id) +
                        (role.team === 'townsfolk' || role.team === 'outsider'
                          ? '_g'
                          : role.team === 'minion' || role.team === 'demon'
                            ? '_e'
                            : '') +
                        '.webp',
                    )
              })`,
            }"
          ></span>
          <span
            class="reminder"
            v-html="role.firstNightReminder"
            v-if="role.firstNightReminder"
          ></span>
        </li>
      </ul>
      <ul class="other">
        <li class="headline">Other Nights</li>
        <li
          v-for="role in rolesOtherNight"
          :key="role.name"
          :class="[role.team]"
        >
          <span
            class="icon"
            v-if="role.id"
            :style="{
              backgroundImage: `url(${
                role.image && grimoire.isImageOptIn
                  ? Array.isArray(role.image)
                    ? role.image[0]
                    : role.image
                  : require(
                      '../../assets/icons/' +
                        (role.imageAlt || role.id) +
                        (role.team === 'townsfolk' || role.team === 'outsider'
                          ? '_g'
                          : role.team === 'minion' || role.team === 'demon'
                            ? '_e'
                            : '') +
                        '.webp',
                    )
              })`,
            }"
          ></span>
          <span class="name">
            {{ role.name }}
            <span
              class="player"
              v-if="
                role.players.length ||
                storyteller.role.id === role.id ||
                extraRoles.some((extraRole) => extraRole.id === role.id)
              "
            >
              <br />
              <small>
                <template
                  v-if="
                    extraRoles.some((extraRole) => extraRole.id === role.id)
                  "
                  >{{
                    "Extra Character" +
                    (role.players.length > 0 || storyteller.role.id === role.id
                      ? ","
                      : "")
                  }}
                </template>
              </small>
              <small>
                <template v-if="storyteller.role.id === role.id">{{
                  storyteller.name + (role.players.length > 0 ? "," : "")
                }}</template>
              </small>
              <small
                v-for="(player, index) in role.players"
                :class="{ dead: player.isDead || player.isInvisibleDead }"
                :key="index"
              >
                <template v-if="!session.isSpectator">
                  <div class="option" @click="setResponded(player, role.id)">
                    <font-awesome-icon
                      :icon="[
                        'fas',
                        !!player.hasResponded[role.id]
                          ? 'square-check'
                          : 'square',
                      ]"
                    />
                    {{ player.name }}
                  </div>
                </template>
                <template v-if="session.isSpectator">{{
                  player.name + (role.players.length > index + 1 ? "," : "")
                }}</template>
              </small>
            </span>
          </span>
          <span
            class="reminder"
            v-html="role.otherNightReminder"
            v-if="role.otherNightReminder"
          ></span>
        </li>
      </ul>
    </div>
  </Modal>
</template>

<script>
import Modal from "./Modal";
import { mapMutations, mapState } from "vuex";

export default {
  components: {
    Modal,
  },
  computed: {
    rolesFirstNight: function () {
      const rolesFirstNight = [];
      // add dawn and dusk to night order sheet
      rolesFirstNight.push(
        {
          id: "dusk",
          name: "Dusk",
          firstNight: 0,
          firstNightReminder:
            "Check that all eyes are closed. Some Travellers & Fabled act.",
          players: [],
        },
        {
          id: "dawn",
          name: "Dawn",
          firstNight: 62,
          firstNightReminder: "Wait a few seconds. Call for eyes open.",
          players: {},
        },
      );
      // add minion / demon infos to night order sheet
      if (this.players.length > 6) {
        rolesFirstNight.push(
          {
            id: "minion",
            name: "Minion Info",
            firstNight: 8,
            firstNightReminder:
              "Wake all Minions: Show the <b>THIS IS THE DEMON</b> token. Point to the Demon. Show the <b>THESE ARE YOUR MINIONS</b> token. Point to the other Minions.",
            team: "minion",
            players: this.players.filter((p) => p.role.team === "minion"),
          },
          {
            id: "demon",
            name: "Demon Info & Bluffs",
            firstNight: 12,
            firstNightReminder:
              "Wake the Demon: Show the <b>THESE ARE YOUR MINIONS</b> token. Point to all Minions. Show the <b>THESE CHARACTERS ARE NOT IN PLAY</b> token. Show 3 not-in-play good character tokens.",
            team: "demon",
            players: this.players.filter((p) => p.role.team === "demon"),
          },
        );
      }
      // add all roles on the script
      this.roles.forEach((role) => {
        const players = this.players.filter((p) => p.role.id === role.id);
        if (
          role.firstNight &&
          role.firstNight >= 0 &&
          (role.team !== "traveler" || players.length)
        ) {
          rolesFirstNight.push(Object.assign({ players }, role));
        }
      });
      if (this.edition.firstNight) {
        // add curated night order if it is a base script
        for (let i = 0; i < this.edition.firstNight.length; i++) {
          rolesFirstNight.forEach((role) => {
            if (role.id === this.edition.firstNight[i]) {
              role.firstNight = i + 2;
            }
          });
        }
      } else {
        // add any off-script roles
        this.players.forEach((player) => {
          if (
            !rolesFirstNight.some((role) => role.id === player.role.id) &&
            player.role.firstNight &&
            player.role.firstNight >= 0
          ) {
            const players = [player];
            rolesFirstNight.push(Object.assign({ players }, player.role));
          }
        });
      }
      this.fabled
        .filter(({ firstNight }) => firstNight)
        .forEach((fabled) => {
          if (fabled.firstNight && fabled.firstNight >= 0) {
            rolesFirstNight.push(Object.assign({ players: [] }, fabled));
          }
        });
      rolesFirstNight.sort((a, b) => a.firstNight - b.firstNight);
      return rolesFirstNight;
    },
    rolesOtherNight: function () {
      const rolesOtherNight = [];
      // add dawn and dusk to night order sheet
      rolesOtherNight.push(
        {
          id: "dusk",
          name: "Dusk",
          otherNight: 0,
          otherNightReminder:
            "Check that all eyes are closed. Some Travellers & Fabled act.",
          players: [],
        },
        {
          id: "dawn",
          name: "Dawn",
          otherNight: 80,
          otherNightReminder:
            "Wait a few seconds. Call for eyes open & immediately say who died.",
          players: [],
        },
      );
      // add all roles on the script
      this.roles.forEach((role) => {
        const players = this.players.filter((p) => p.role.id === role.id);
        if (
          role.otherNight &&
          role.otherNight >= 0 &&
          (role.team !== "traveler" || players.length)
        ) {
          rolesOtherNight.push(Object.assign({ players }, role));
        }
      });
      if (this.edition.otherNight) {
        // add curated night order if it is a base script
        for (let i = 0; i < this.edition.otherNight.length; i++) {
          rolesOtherNight.forEach((role) => {
            if (role.id === this.edition.otherNight[i]) {
              role.otherNight = i + 2;
            }
          });
        }
      } else {
        // add any off-script roles
        this.players.forEach((player) => {
          if (
            !rolesOtherNight.some((role) => role.id === player.role.id) &&
            player.role.otherNight &&
            player.role.otherNight >= 0
          ) {
            const players = [player];
            rolesOtherNight.push(Object.assign({ players }, player.role));
          }
        });
      }
      this.fabled
        .filter(({ otherNight }) => otherNight)
        .forEach((fabled) => {
          if (fabled.otherNight && fabled.otherNight >= 0) {
            rolesOtherNight.push(Object.assign({ players: [] }, fabled));
          }
        });
      rolesOtherNight.sort((a, b) => a.otherNight - b.otherNight);
      return rolesOtherNight;
    },
    ...mapState(["roles", "modals", "edition", "grimoire", "session"]),
    ...mapState("players", ["players", "storyteller", "extraRoles", "fabled"]),
  },
  methods: {
    setResponded(player, roleId) {
      const hasResponded = { ...player.hasResponded };
      hasResponded[roleId] = !hasResponded[roleId];
      this.$store.commit("players/update", {
        player: player,
        property: "hasResponded",
        value: hasResponded,
      });
    },
    ...mapMutations(["toggleModal"]),
  },
};
</script>

