<template>
  <Modal class="reminder" v-if="modals.reminder" @close="close">
    <h3>Choose a Reminder Token:</h3>
    <ul class="reminders">
      <li
        v-for="reminder in availableReminders"
        class="reminder"
        :class="[reminder.role]"
        :key="reminder.role + ' ' + reminder.name"
        @click="addReminder(reminder)"
      >
        <span
          class="icon"
          :style="{
            backgroundImage: `url(${
              reminder.image && grimoire.isImageOptIn
                ? Array.isArray(reminder.image)
                  ? reminder.image[0]
                  : reminder.image
                : require(
                    '../../assets/icons/' +
                      (reminder.imageAlt || reminder.role) +
                      (reminder.team === 'townsfolk' ||
                      reminder.team === 'outsider'
                        ? '_g'
                        : reminder.team === 'minion' ||
                            reminder.team === 'demon'
                          ? '_e'
                          : '') +
                      '.webp',
                  )
            })`,
          }"
        ></span>
        <svg viewBox="10 10 180 180">
          <path
            d="M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0"
            id="circle"
            fill="transparent"
            transform="rotate(48, 100, 100)"
          />
          <text width="150" dx="100%" text-anchor="middle" class="text">
            <textPath xlink:href="#circle">
              {{ reminder.name }}
            </textPath>
          </text>
        </svg>
      </li>
    </ul>
    <div class="button-group">
      <span
        class="button purple-hover"
        :class="{ purple: tab === 'relevantReminders' }"
        @click="tab = 'relevantReminders'"
        >Relevant Reminders</span
      >
      <span
        class="button purple-hover"
        :class="{ purple: tab === 'otherReminders' }"
        @click="tab = 'otherReminders'"
        >Other Reminders</span
      >
    </div>
  </Modal>
</template>

<script>
import Modal from "./Modal";
import { mapMutations, mapState } from "vuex";

/**
 * Helper function that maps a reminder name with a role-based object that provides necessary visual data.
 * @param role The role for which the reminder should be generated
 * @return {function(*): {image: string|string[]|string|*, role: *, name: *, imageAlt: string|*}}
 */
const mapReminder =
  ({ id, image, imageAlt, team }) =>
  (name) => ({
    role: id,
    image,
    imageAlt,
    team,
    name,
  });

export default {
  components: { Modal },
  props: ["playerIndex", "isDeath"],
  computed: {
    availableReminders() {
      let relevantReminders = [];
      let otherReminders = [];
      const { players } = this.$store.state.players;
      this.$store.state.roles.forEach((role) => {
        // add reminders from player roles, storyteller ability, and extra roles
        if (
          players.some((p) => p.role.id === role.id) ||
          this.$store.state.players.storyteller.role.id === role.id ||
          this.$store.state.players.extraRoles.some((r) => r.id === role.id)
        ) {
          role.reminders.map(mapReminder(role)).forEach((reminder1) => {
            if (
              !relevantReminders.some(
                (reminder2) =>
                  reminder2.name === reminder1.name &&
                  reminder2.role === reminder1.role,
              )
            ) {
              relevantReminders.push(reminder1);
            }
          });
        } else {
          role.reminders.map(mapReminder(role)).forEach((reminder1) => {
            if (
              !otherReminders.some(
                (reminder2) =>
                  reminder2.name === reminder1.name &&
                  reminder2.role === reminder1.role,
              )
            ) {
              otherReminders.push(reminder1);
            }
          });
        }
        // add global reminders
        if (role.remindersGlobal && role.remindersGlobal.length) {
          role.remindersGlobal.map(mapReminder(role)).forEach((reminder1) => {
            if (
              !relevantReminders.some(
                (reminder2) =>
                  reminder2.name === reminder1.name &&
                  reminder2.role === reminder1.role,
              )
            ) {
              relevantReminders.push(reminder1);
            }
          });
        }
      });
      // add fabled reminders
      this.$store.state.players.fabled.forEach((role) => {
        role.reminders.map(mapReminder(role)).forEach((reminder1) => {
          if (
            !relevantReminders.some(
              (reminder2) =>
                reminder2.name === reminder1.name &&
                reminder2.role === reminder1.role,
            )
          ) {
            relevantReminders.push(reminder1);
          }
        });
      });
      // add out of script traveler reminders
      this.$store.state.otherTravelers.forEach((role) => {
        if (players.some((p) => p.role.id === role.id)) {
          role.reminders.map(mapReminder(role)).forEach((reminder1) => {
            if (
              !relevantReminders.some(
                (reminder2) =>
                  reminder2.name === reminder1.name &&
                  reminder2.role === reminder1.role,
              )
            ) {
              relevantReminders.push(reminder1);
            }
          });
        }
      });

      relevantReminders.push({ role: "custom", name: "Custom Note" });
      otherReminders.push({ role: "custom", name: "Custom Note" });

      if (this.isDeath) {
        relevantReminders = relevantReminders.filter(
          (reminder) => reminder.name === "Dead",
        );
        otherReminders = otherReminders.filter(
          (reminder) => reminder.name === "Dead",
        );
      }

      if (this.tab === "relevantReminders") return relevantReminders;
      else return otherReminders;
    },
    ...mapState(["modals", "grimoire"]),
    ...mapState("players", ["players"]),
  },
  data() {
    return {
      tab: "relevantReminders",
    };
  },
  methods: {
    addReminder(reminder) {
      let player;
      if (this.playerIndex > 19) {
        player = this.$store.state.players.storyteller;
      } else {
        player = this.$store.state.players.players[this.playerIndex];
      }
      const newReminder = { ...reminder };
      let value;
      if (newReminder.role === "custom") {
        const name = prompt("Add a custom reminder note");
        if (!name) return;
        value = [...player.reminders, { role: "custom", name }];
      } else {
        value = [...player.reminders, newReminder];
      }
      if (this.playerIndex > 19) {
        this.$store.commit("players/updateStoryteller", {
          property: "reminders",
          value,
        });
      } else {
        this.$store.commit("players/update", {
          player,
          property: "reminders",
          value,
        });
      }
      this.close();
    },
    close() {
      this.tab = "relevantReminders";
      this.toggleModal("reminder");
    },
    ...mapMutations(["toggleModal"]),
  },
};
</script>

<style scoped lang="scss">
ul.reminders .reminder {
  background: url("../../assets/reminder.webp") center center;
  background-size: 100%;
  width: 14vh;
  height: 14vh;
  max-width: 100px;
  max-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1%;
  padding-bottom: 0;

  border-radius: 50%;
  border: 2px solid black;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  line-height: 100%;
  transition: transform 500ms ease;

  .icon {
    position: absolute;
    top: 0;
    width: 90%;
    height: 90%;
    background-size: 100%;
    background-position: center center;
    background-repeat: no-repeat;
  }

  .text {
    fill: white;
    font-family: "Trade Gothic LT Std", sans-serif;
    font-size: 28px;
    font-weight: bold;
    letter-spacing: 1px;
    text-align: center;
    height: 100%;
    width: 100%;
    position: absolute;
  }

  &:hover {
    transform: scale(1.2);
  }
}
</style>
