<template>
  <Modal v-if="isDisplayed" @close="close">
    <h3>Choose a New Extra Character:</h3>
    <ul class="tokens">
      <li
        v-for="role in availableRoles"
        :class="[role.team, { match: queryMatches(role.name) }]"
        :key="role.id"
        @click="setRole(role)"
      >
        <Token :role="role" />
      </li>
    </ul>
    <input
      ref="searchInput"
      class="role-search"
      placeholder="Search"
      v-model="query"
      @keyup="keyup"
    />
  </Modal>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import Modal from "./Modal";
import Token from "../Token";

export default {
  components: { Token, Modal },
  computed: {
    availableRoles() {
      const roles = [];
      this.$store.state.roles.forEach((role) => {
        // don't show roles that are already an extra character
        if (
          !this.$store.state.players.extraRoles.some(
            (extraRole) => extraRole.id === role.id,
          )
        ) {
          roles.push(role);
        }
      });
      return roles;
    },
    isDisplayed() {
      return this.modals.extraRoles && this.availableRoles.length;
    },
    ...mapState(["modals", "roles", "grimoire"]),
  },
  data() {
    return {
      query: "",
    };
  },
  methods: {
    setRole(role) {
      this.$store.commit("players/setExtraRoles", {
        role: role,
      });
      this.close();
    },
    close() {
      this.query = "";
      this.toggleModal("extraRoles");
    },
    queryMatches(name) {
      // A search query matches if, after removing all non-word characters,
      // it is a case-insensitive prefix of the character name.
      const simplify = (str) => str.replaceAll(/\W+/g, "").toLowerCase();
      return simplify(name || "").startsWith(simplify(this.query));
    },
    keyup(event) {
      // Allow Escape for modal dialog dismissal.
      if (event.key === "Esc" || event.key === "Escape") return;

      event.stopPropagation();

      // If there's a unique match and the user presses Enter, select that role.
      if (event.key === "Enter") {
        const matchingRoles = this.availableRoles.filter((r) =>
          this.queryMatches(r.name),
        );
        if (matchingRoles.length === 1) {
          this.setRole(matchingRoles[0]);
        }
      }
    },
    ...mapMutations(["toggleModal"]),
  },
  watch: {
    isDisplayed(shown) {
      if (shown) this.$nextTick(() => this.$refs.searchInput.focus());
    },
  },
};
</script>

