<template>
  <div id="controls">
    <span
      class="session"
      :class="{
        spectator: session.isSpectator,
        reconnecting: session.isReconnecting,
      }"
      v-if="session.sessionId"
      @click="leaveSession"
      :title="`${session.playerCount} other players in this session${
        session.ping ? ' (' + session.ping + 'ms latency)' : ''
      }`"
    >
      <font-awesome-icon icon="tower-cell" />
      {{ session.playerCount }}
    </span>
    <div class="menu" :class="{ open: grimoire.isMenuOpen }">
      <font-awesome-icon icon="gear" @click="toggleMenu" />
      <ul>
        <li class="tabs" :class="tab">
          <font-awesome-icon icon="book-open" @click="tab = 'grimoire'" />
          <font-awesome-icon icon="tower-cell" @click="tab = 'session'" />
          <font-awesome-icon
            icon="users"
            v-if="!session.isSpectator"
            @click="tab = 'players'"
          />
          <font-awesome-icon
            icon="masks-theater"
            v-if="!session.isSpectator"
            @click="tab = 'characters'"
          />
          <font-awesome-icon icon="question" @click="tab = 'help'" />
        </li>

        <template v-if="tab === 'grimoire'">
          <!-- Grimoire -->
          <li class="headline">Grimoire</li>
          <li @click="clearRoles" v-if="players.length && !session.reveal">
            Reset Grimoire
            <em><font-awesome-icon icon="trash-can" /></em>
          </li>
          <li @click="toggleGrimoire" v-if="players.length">
            <template v-if="!grimoire.isPublic">Show Town Square</template>
            <template v-if="grimoire.isPublic">Show Grimoire</template>
            <em>[G]</em>
          </li>
          <li @click="toggleFabled" v-if="players.length">
            <template v-if="!grimoire.isFabledOpen">Show Fabled</template>
            <template v-if="grimoire.isFabledOpen">Hide Fabled</template>
            <em>[F]</em>
          </li>
          <li @click="toggleBluffs" v-if="players.length">
            <template v-if="!grimoire.isBluffsOpen">Show Demon Bluffs</template>
            <template v-if="grimoire.isBluffsOpen">Hide Demon Bluffs</template>
            <em>[B]</em>
          </li>
          <li @click="toggleExtraBluffs" v-if="players.length">
            Extra Bluffs
            <em>
              <font-awesome-icon
                :icon="[
                  'fas',
                  grimoire.isExtraBluffs ? 'square-check' : 'square',
                ]"
              />
            </em>
          </li>
          <li @click="toggleNight" v-if="!session.isSpectator">
            <template v-if="!grimoire.isNight">Switch to Night</template>
            <template v-if="grimoire.isNight">Switch to Day</template>
            <em>[S]</em>
          </li>
          <li @click="toggleNightOrder" v-if="players.length">
            Night Order
            <em>
              <font-awesome-icon
                :icon="[
                  'fas',
                  grimoire.isNightOrder ? 'square-check' : 'square',
                ]"
              />
            </em>
          </li>
          <li v-if="players.length">
            Token Scale
            <em>
              <font-awesome-icon
                @click="setZoom(Math.min(Math.max(grimoire.zoom - 1, -8), 10))"
                icon="magnifying-glass-minus"
              />
              {{ Math.round(100 + grimoire.zoom * 10) }}%
              <font-awesome-icon
                @click="setZoom(Math.min(Math.max(grimoire.zoom + 1, -8), 10))"
                icon="magnifying-glass-plus"
              />
            </em>
          </li>
          <li>
            <small>Default Background</small>
            <em>
              <font-awesome-icon
                @click="
                  setDefaultBackgroundIndex(
                    (edition.isOfficial
                      ? grimoire.defaultBaseBackgroundIndex
                      : grimoire.defaultCustomBackgroundIndex) - 1,
                  )
                "
                icon="circle-chevron-left"
              />
              {{
                (edition.isOfficial
                  ? grimoire.defaultBaseBackgroundIndex
                  : grimoire.defaultCustomBackgroundIndex) + 1
              }}
              <font-awesome-icon
                @click="
                  setDefaultBackgroundIndex(
                    (edition.isOfficial
                      ? grimoire.defaultBaseBackgroundIndex
                      : grimoire.defaultCustomBackgroundIndex) + 1,
                  )
                "
                icon="circle-chevron-right"
              />
            </em>
          </li>
          <li @click="setBackground">
            Custom Background
            <em><font-awesome-icon icon="image" /></em>
          </li>
          <li v-if="!edition.isOfficial" @click="imageOptIn">
            Show Custom Images
            <em
              ><font-awesome-icon
                :icon="[
                  'fas',
                  grimoire.isImageOptIn ? 'square-check' : 'square',
                ]"
            /></em>
          </li>
          <li @click="toggleStatic">
            Disable Animations
            <em
              ><font-awesome-icon
                :icon="['fas', grimoire.isStatic ? 'square-check' : 'square']"
            /></em>
          </li>
          <li @click="toggleMuted">
            <template v-if="!grimoire.isMuted">Mute Sounds</template>
            <template v-if="grimoire.isMuted">Unmute Sounds</template>
            <em
              ><font-awesome-icon
                :icon="[
                  'fas',
                  grimoire.isMuted ? 'volume-xmark' : 'volume-high',
                ]"
            /></em>
          </li>
        </template>

        <template v-if="tab === 'session'">
          <!-- Session -->
          <li class="headline" v-if="session.sessionId">
            {{ session.isSpectator ? "Playing" : "Hosting" }}
          </li>
          <li class="headline" v-else>Live Session</li>
          <template v-if="!session.sessionId">
            <li @click="hostSession">Host (Storyteller)<em>[H]</em></li>
            <li @click="joinSession">Join (Player)<em>[J]</em></li>
          </template>
          <template v-else>
            <li v-if="session.ping">
              Delay to {{ session.isSpectator ? "Host" : "Players" }}
              <em>{{ session.ping }}ms</em>
            </li>
            <li @click="copySessionUrl">
              Copy Player Link
              <em><font-awesome-icon icon="copy" /></em>
            </li>
            <li
              v-if="session.voteHistory.length || !session.isSpectator"
              @click="toggleModal('voteHistory')"
            >
              Vote History<em>[V]</em>
            </li>
            <li @click="leaveSession">
              Leave Session
              <em>{{ session.sessionId }}</em>
            </li>
          </template>
        </template>

        <template v-if="tab === 'players' && !session.isSpectator">
          <!-- Users -->
          <li class="headline">Players</li>
          <li @click="addPlayer" v-if="players.length < 20 && !session.reveal">
            Add<em>[A]</em>
          </li>
          <li
            @click="randomizeSeatings"
            v-if="players.length > 2 && !session.reveal"
          >
            Randomize Seating
            <em><font-awesome-icon icon="dice" /></em>
          </li>
          <li @click="clearPronouns" v-if="players.length">
            Clear Pronouns
            <em><font-awesome-icon icon="venus-mars" /></em>
          </li>
          <li @click="forceHandsDown" v-if="players.length">
            Force Hands Down
            <em><font-awesome-icon icon="hand" /></em>
          </li>
          <li @click="clearPlayers" v-if="players.length && !session.reveal">
            Remove All
            <em><font-awesome-icon icon="trash-can" /></em>
          </li>
        </template>

        <template v-if="tab === 'characters' && !session.isSpectator">
          <!-- Characters -->
          <li class="headline">Characters</li>
          <li @click="toggleModal('edition')" v-if="!session.reveal">
            Select Edition
            <em>[E]</em>
          </li>
          <li
            @click="toggleModal('roles')"
            v-if="players.length > 4 && !session.reveal"
          >
            Choose & Assign
            <em>[C]</em>
          </li>
          <li @click="toggleModal('fabled')">
            Add Fabled
            <em><font-awesome-icon icon="dragon" /></em>
          </li>
          <li @click="toggleModal('extraRoles')">
            Add Extra Characters
            <em><font-awesome-icon icon="circle-plus" /></em>
          </li>
          <li
            v-if="session.sessionId && players.length > 4 && !session.reveal"
            @click="distributeRoles"
          >
            Send Characters
            <em><font-awesome-icon icon="paper-plane" /></em>
          </li>
          <li
            v-if="session.sessionId && players.length > 4 && !session.reveal"
            @click="revealGrimoire"
          >
            Reveal Grimoire
            <em><font-awesome-icon icon="book-skull" /></em>
          </li>
        </template>

        <template v-if="tab === 'help'">
          <!-- Help -->
          <li class="headline">Help</li>
          <li @click="toggleModal('reference')">
            Reference Sheet
            <em>[R]</em>
          </li>
          <li @click="toggleModal('nightOrder')">
            Night Order Sheet
            <em>[N]</em>
          </li>
          <li @click="toggleModal('gameState')">
            Game State JSON
            <em><font-awesome-icon icon="file-code" /></em>
          </li>
        </template>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  computed: {
    ...mapState(["grimoire", "session", "edition"]),
    ...mapState("players", ["players"]),
  },
  data() {
    return {
      tab: "grimoire",
    };
  },
  methods: {
    setDefaultBackgroundIndex(newBackground) {
      if (this.edition.isOfficial) {
        this.$store.commit("setDefaultBaseBackgroundIndex", newBackground);
      } else {
        this.$store.commit("setDefaultCustomBackgroundIndex", newBackground);
      }
    },
    setBackground() {
      const background = prompt(
        "Enter a custom background URL or leave blank to reset to default background",
      );
      if (background || background === "") {
        this.$store.commit("setBackground", background);
      }
    },
    hostSession() {
      if (this.session.sessionId) return;
      const sessionId = prompt(
        "Enter a channel number / name for your session",
        Math.round(Math.random() * 10000),
      );
      if (sessionId) {
        this.$store.commit("session/clearVoteHistory");
        this.$store.commit("session/setSpectator", false);
        this.$store.commit("session/setSessionId", sessionId);
        this.copySessionUrl();
      }
    },
    copySessionUrl() {
      const url = window.location.href.split("#")[0];
      const link = url + "#" + this.session.sessionId;
      navigator.clipboard.writeText(link);
    },
    distributeRoles() {
      if (this.session.isSpectator || this.session.reveal) return;
      const popup =
        "Do you want to distribute assigned characters to all SEATED players? Note that this will not inform players of any modified alignments.";
      if (confirm(popup)) {
        this.$store.commit("session/distributeRoles", true);
        setTimeout(
          (() => {
            this.$store.commit("session/distributeRoles", false);
          }).bind(this),
          2000,
        );
      }
    },
    revealGrimoire() {
      if (this.session.isSpectator || this.session.reveal) return;
      this.$store.commit("session/setReveal", { isRevealInProgress: true });
      this.$store.commit("session/setMarkedPlayer", -1);
      setTimeout(
        (() => {
          this.players.forEach((player) => {
            player.reminders.toReversed().forEach((reminder) => {
              const role =
                this.$store.state.roles.get(reminder.role) ||
                this.$store.getters.rolesJSONbyId.get(reminder.role) ||
                {};
              if (
                role &&
                role.remindersGlobal &&
                role.remindersGlobal[0] === reminder.name
              ) {
                const alignment = player.alignment;
                this.$store.commit("players/update", {
                  player,
                  property: "role",
                  value: role,
                });
                this.$store.commit("players/update", {
                  player,
                  property: "alignment",
                  value: alignment,
                });
              }
            });
          });
        }).bind(this),
        62.5 * this.players.length + 250,
      );
    },
    imageOptIn() {
      const popup =
        "Are you sure you want to allow custom images? A malicious script file author might track your IP address this way.";
      if (this.grimoire.isImageOptIn || confirm(popup)) {
        this.toggleImageOptIn();
      }
    },
    joinSession() {
      if (this.session.sessionId) return this.leaveSession();
      let sessionId = prompt(
        "Enter the channel number / name of the session you want to join",
      );
      if (sessionId.match(/^https?:\/\//i)) {
        sessionId = sessionId.split("#").pop();
      }
      if (sessionId) {
        this.$store.commit("session/clearVoteHistory");
        this.$store.commit("session/setSpectator", true);
        this.$store.commit("toggleGrimoire", false);
        this.$store.commit("session/setSessionId", sessionId);
      }
    },
    leaveSession() {
      if (confirm("Are you sure you want to leave the active live game?")) {
        this.$store.commit("session/setSpectator", false);
        this.$store.commit("session/setSessionId", "");
      }
    },
    addPlayer() {
      if (
        this.session.isSpectator ||
        this.players.length >= 20 ||
        this.session.reveal
      )
        return;
      const name = prompt("Player name");
      if (name) {
        this.$store.commit("players/add", name);
      }
    },
    randomizeSeatings() {
      if (this.session.isSpectator || this.session.reveal) return;
      if (confirm("Are you sure you want to randomize seatings?")) {
        this.$store.dispatch("players/randomize");
      }
    },
    clearPlayers() {
      if (this.session.isSpectator || this.session.reveal) return;
      if (confirm("Are you sure you want to remove all players?")) {
        // abort vote if in progress
        if (this.session.nomination) {
          this.$store.commit("session/nomination");
        }
        this.$store.commit("players/clear");
        this.$store.commit("session/setMarkedPlayer", -1);
        this.$store.commit("toggleFabled", true);
        this.$store.commit("toggleBluffs", true);
        this.$store.commit("toggleExtraBluffs", false);
      }
    },
    clearPronouns() {
      if (this.session.isSpectator) return;
      if (confirm("Are you sure you want to remove all player pronouns?")) {
        const property = "pronouns";
        const value = "";
        this.players.forEach((player) => {
          this.$store.commit("players/update", {
            player: player,
            property,
            value,
          });
        });
      }
    },
    clearRoles() {
      if (
        !this.session.reveal &&
        ((this.session.isSpectator &&
          confirm("Are you sure you want to reset the grimoire?")) ||
          (!this.session.isSpectator &&
            confirm(
              "Are you sure you want to reset the grimoire? This will also reset the grimoire for all players.",
            )))
      ) {
        this.$store.dispatch("players/clearRoles");
        this.$store.commit("session/setMarkedPlayer", -1);
        this.$store.commit("toggleFabled", true);
        this.$store.commit("toggleBluffs", true);
        this.$store.commit("toggleExtraBluffs", false);
      }
    },
    forceHandsDown() {
      if (this.session.isSpectator) return;
      if (
        confirm(
          "Are you sure you want to force all players to lower their hands?",
        )
      ) {
        this.$store.commit("session/setHandsRaised", []);
      }
    },
    toggleNight() {
      this.$store.commit("toggleNight");
      this.grimoire.isNightOrder = this.grimoire.isNight;
      if (this.grimoire.isNight) {
        this.$store.commit("session/setMarkedPlayer", -1);
      }
    },
    ...mapMutations([
      "toggleGrimoire",
      "toggleFabled",
      "toggleBluffs",
      "toggleExtraBluffs",
      "toggleMenu",
      "toggleImageOptIn",
      "toggleMuted",
      "toggleNightOrder",
      "toggleStatic",
      "setZoom",
      "toggleModal",
    ]),
  },
};
</script>

<style scoped lang="scss">
@import "../vars.scss";

