#controls {
  position: absolute;
  right: 3px;
  top: 3px;
  text-align: right;
  padding-right: 60px;
  z-index: 75;

  svg {
    filter: drop-shadow(0 0 5px rgba(0, 0, 0, 1));
    &.success {
      animation: greenToWhite 1s normal forwards;
      animation-iteration-count: 1;
    }
  }

  > span {
    display: inline-block;
    cursor: pointer;
    z-index: 5;
    margin-top: 9px;
    margin-left: 10px;
    filter: drop-shadow(0 0 2px rgba(0, 0, 0, 1));
    text-shadow: 0 0 5px black;
  }

  span.session {
    color: $light-purple;
    font-weight: bold;
    &.spectator {
      color: $gold;
    }
    &.reconnecting {
      animation: blink 1s infinite;
    }
  }
}

@keyframes blink {
  50% {
    opacity: 0.5;
    color: gray;
  }
}

