  width: 245px;
  transform-origin: 219.5px 24.6px;
  transition: transform 500ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
  transform: rotate(-90deg);
  position: absolute;
  right: 0;
  top: 0;

  &.open {
    transform: rotate(0deg);
  }

  &.open ul {
    box-shadow: 0 0 10px black;
  }

  > svg {
    cursor: pointer;
    background: rgba(0, 0, 0, 0.5);
    border: 3px solid black;
    width: 29.5px;
    height: 26px;
    margin-bottom: -6px;
    border-bottom: 0;
    border-radius: 10px 10px 0 0;
    padding: 9px 8px 18px;
  }

  a {
    color: white;
    text-decoration: none;
    &:hover {
      color: $gold;
    }
  }

  ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
    overflow: hidden;
    border: 3px solid black;
    border-radius: 10px 0 10px 10px;
    transition: box-shadow 500ms ease-in-out;

    li {
      padding: 2px 5px;
      color: white;
      text-align: left;
      font-family: "Goudy StM", sans-serif;
      font-size: 90%;
      background: rgba(0, 0, 0, 0.7);
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-height: 30px;

      &.tabs {
        display: flex;
        padding: 0;
        svg {
          flex: 100%;
          height: 23px;
          border-bottom: 3px solid black;
          border-left: 3px solid black;
          padding: 5px 0;
          cursor: pointer;
          transition: color 250ms;
          &:hover {
            color: $gold;
          }
          &:first-child {
            border-left: 0;
          }
        }
        &.grimoire .fa-book-open,
        &.players .fa-users,
        &.characters .fa-masks-theater,
        &.session .fa-tower-cell,
        &.help .fa-question {
          background: linear-gradient(
            to bottom,
            $gold 0%,
            rgba(0, 0, 0, 0.5) 100%
          );
        }
      }

      &:not(.headline):not(.tabs):hover {
        cursor: pointer;
        color: $gold;
      }

      em {
        flex-grow: 0;
        font-style: normal;
        margin-left: 10px;
        font-size: 80%;
        svg {
          vertical-align: middle;
        }
      }
    }

    .headline {
      font-family:
        Goudy StM,
        sans-serif;
      letter-spacing: 1px;
      padding: 0 10px;
      text-align: center;
      justify-content: center;
      background: linear-gradient(
        to right,
        $gold 0%,
        rgba(0, 0, 0, 0.5) 20%,
        rgba(0, 0, 0, 0.5) 80%,
        $purple 100%
      );
