<template>
  <Modal v-if="isDisplayed" @close="close">
    <h3>Choose a Fabled Character to Add to the Game:</h3>
    <ul class="tokens">
      <li
        v-for="role in fabled"
        :class="{ match: queryMatches(role.name) }"
        :key="role.id"
        @click="setFabled(role)"
      >
        <Token :role="role" />
      </li>
    </ul>
    <input
      ref="searchInput"
      class="role-search"
      placeholder="Search"
      v-model="query"
      @keyup="keyup"
    />
  </Modal>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import Modal from "./Modal";
import Token from "../Token";

export default {
  components: { Token, Modal },
  computed: {
    ...mapState(["modals", "fabled", "grimoire"]),
    fabled() {
      const fabled = [];
      this.$store.state.fabled.forEach((role) => {
        // don't show fabled that are already in play
        if (
          !this.$store.state.players.fabled.some(
            (fable) => fable.id === role.id,
          )
        ) {
          fabled.push(role);
        }
      });
      return fabled;
    },
    isDisplayed() {
      return this.modals.fabled;
    },
  },
  data() {
    return {
      query: "",
    };
  },
  methods: {
    setFabled(role) {
      this.$store.commit("players/setFabled", {
        fabled: role,
      });
      this.$store.commit("toggleFabled", true);
      this.close();
    },
    close() {
      this.query = "";
      this.toggleModal("fabled");
    },
    queryMatches(name) {
      // A search query matches if, after removing all non-word characters,
      // it is a case-insensitive prefix of the character name.
      const simplify = (str) => str.replaceAll(/\W+/g, "").toLowerCase();
      return simplify(name || "").startsWith(simplify(this.query));
    },
    keyup(event) {
      // Allow Escape for modal dialog dismissal.
      if (event.key === "Esc" || event.key === "Escape") return;

      event.stopPropagation();

      // If there's a unique match and the user presses Enter, select that role.
      if (event.key === "Enter") {
        const matchingRoles = this.fabled.filter((r) =>
          this.queryMatches(r.name),
        );
        if (matchingRoles.length === 1) {
          this.setFabled(matchingRoles[0]);
        }
      }
    },
    ...mapMutations(["toggleModal"]),
  },
  watch: {
    isDisplayed(shown) {
      if (shown) this.$nextTick(() => this.$refs.searchInput.focus());
    },
  },
};
</script>

