<template>
  <Modal v-if="isDisplayed" @close="close">
    <h3>
      Choose a New
      {{
        playerIndex > 19
          ? "Minion Ability for the Storyteller"
          : playerIndex >= 0 && players.length
            ? "Character for " + players[playerIndex].name
            : "Demon Bluff"
      }}:
    </h3>
    <ul class="tokens">
      <li
        v-for="role in displayedRoles"
        :class="[role.team, { match: queryMatches(role.name) }]"
        :key="role.id"
        @click="setRole(role)"
      >
        <Token :role="role" />
      </li>
    </ul>
    <div
      class="button-group"
      v-if="
        playerIndex >= 0 &&
        playerIndex <= 19 &&
        otherTravelers.size &&
        !session.isSpectator
      "
    >
      <span
        class="button purple-hover"
        :class="{ purple: tab === 'editionRoles' }"
        @click="tab = 'editionRoles'"
        >Edition Roles</span
      >
      <span
        class="button purple-hover"
        :class="{ purple: tab === 'otherTravelers' }"
        @click="tab = 'otherTravelers'"
        >Other Travellers</span
      >
    </div>
    <input
      ref="searchInput"
      class="role-search"
      placeholder="Search"
      v-model="query"
      @keyup="keyup"
    />
  </Modal>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import Modal from "./Modal";
import Token from "../Token";

export default {
  components: { Token, Modal },
  props: ["playerIndex"],
  computed: {
    availableRoles() {
      const availableRoles = [];
      const players = this.$store.state.players.players;
      const bluffs = this.$store.state.players.bluffs;
      this.$store.state.roles.forEach((role) => {
        // don't show bluff roles that are already assigned to players, or are not townsfolk/outsiders
        if (
          (this.playerIndex >= 0 && this.playerIndex <= 19) ||
          (this.playerIndex > 19 && role.team === "minion") ||
          (this.playerIndex < 0 &&
            role.team !== "minion" &&
            role.team !== "demon" &&
            role.team !== "traveler" &&
            (this.playerIndex < -3 ||
              (!players.some((player) => player.role.id === role.id) &&
                !(
                  this.grimoire.isExtraBluffs ? bluffs : bluffs.slice(0, 3)
                ).some((bluff) => bluff.id === role.id))))
        ) {
          availableRoles.push(role);
        }
      });
      availableRoles.push({});
      return availableRoles;
    },
    isDisplayed() {
      return this.modals.role && this.availableRoles.length;
    },
    displayedRoles() {
      if (this.tab === "editionRoles" || !this.otherTravelers.size)
        return this.availableRoles;
      else return [...this.otherTravelers.values()];
    },
    ...mapState(["grimoire", "modals", "roles", "session"]),
    ...mapState("players", ["players"]),
    ...mapState(["otherTravelers"]),
  },
  data() {
    return {
      tab: "editionRoles",
      query: "",
    };
  },
  methods: {
    setRole(role) {
      if (this.playerIndex < 0) {
        // assign to bluff slot (index < 0)
        this.$store.commit("players/setBluff", {
          index: this.playerIndex * -1 - 1,
          role,
        });
      } else {
        if (
          (this.session.isSpectator && role.team === "traveler") ||
          (this.playerIndex > 19 && role.team && role.team !== "minion")
        )
          return;
        // assign to player
        if (this.playerIndex > 19) {
          this.$store.commit("players/updateStoryteller", {
            property: "role",
            value: role,
          });
        } else {
          const player = this.$store.state.players.players[this.playerIndex];
          if (this.session.isSpectator && player.role.team === "traveler") {
            this.close();
            return;
          }
          this.$store.commit("players/update", {
            player,
            property: "role",
            value: role,
          });
        }
      }
      this.close();
    },
    close() {
      this.tab = "editionRoles";
      this.query = "";
      this.toggleModal("role");
    },
    queryMatches(name) {
      // A search query matches if, after removing all non-word characters,
      // it is a case-insensitive prefix of the character name.
      const simplify = (str) => str.replaceAll(/\W+/g, "").toLowerCase();
      return simplify(name || "").startsWith(simplify(this.query));
    },
    keyup(event) {
      // Allow Escape for modal dialog dismissal.
      if (event.key === "Esc" || event.key === "Escape") return;

      event.stopPropagation();

      // If there's a unique match and the user presses Enter, select that role.
      if (event.key === "Enter") {
        const matchingRoles = this.displayedRoles.filter((r) =>
          this.queryMatches(r.name),
        );
        if (matchingRoles.length === 1) {
          this.setRole(matchingRoles[0]);
        }
      }
    },
    ...mapMutations(["toggleModal"]),
  },
  watch: {
    isDisplayed(shown) {
      if (shown) this.$nextTick(() => this.$refs.searchInput.focus());
    },
  },
};
</script>

