<template>
  <div class="center">
    <div class="result">
      <div class="winning-team" v-if="session.winningTeam">
        <span :class="session.winningTeam">
          {{ session.winningTeam === "e" ? "EVIL" : "GOOD" }}
        </span>
        <br />
        <small>HAS WON</small>
      </div>
    </div>
    <div class="overlay" v-if="!session.isSpectator">
      REVEAL GRIMOIRE<br />
      Reveal the game result below and each<br />
      player's character by clicking on their token.<br />

      <div class="button-group">
        <div
          class="button gold"
          v-if="!session.winningTeam"
          @click="gameResult('g')"
        >
          Good Won
        </div>
        <div
          class="button purple"
          v-if="!session.winningTeam"
          @click="gameResult('e')"
        >
          Evil Won
        </div>
        <div class="button" @click="cancel()">Cancel</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["session"]),
  },
  methods: {
    gameResult(team) {
      this.$store.commit("session/setReveal", { winningTeam: team });
    },
    cancel() {
      this.$store.commit("session/setReveal", { isRevealInProgress: false });
    },
  },
};
</script>

