<template>
  <li :style="zoom">
    <div
      ref="player"
      class="player"
      @focusout="isMenuOpen = false"
      tabindex="-1"
      :class="[
        {
          dead: player.isDead,
          'invisible-dead': player.isInvisibleDead,
          marked:
            (!session.isSpectator || session.isVoteWatchingAllowed) &&
            session.markedPlayer === index,
          'hand-raised': session.handsRaised[index],
          'menu-open': isMenuOpen && !session.reveal,
          'no-vote': player.isVoteless,
          you: session.sessionId && player.id && player.id === session.playerId,
          'vote-yes':
            (!session.isSpectator ||
              session.isVoteWatchingAllowed ||
              player.id === session.playerId) &&
            session.votes[index],
          'vote-lock': voteLocked,
          revealed: session.revealedPlayers[index],
        },
        player.role.team,
        player.alignment,
      ]"
    >
      <div class="shroud" @click="clickShroud()">
        <font-awesome-icon icon="eye-slash" />
      </div>
      <div class="life" @click="toggleStatus()"></div>

      <div
        class="night-order first"
        :class="{
          shown:
            nightOrder.get(player).first &&
            grimoire.isNightOrder &&
            !grimoire.isPublic &&
            !session.reveal,
        }"
      >
        <em>{{ nightOrder.get(player).first }}</em>
        <span class="description" v-if="player.role.firstNightReminder">
          <span v-html="player.role.firstNightReminder"></span>
        </span>
      </div>
      <div
        class="night-order other"
        :class="{
          shown:
            nightOrder.get(player).other &&
            grimoire.isNightOrder &&
            !grimoire.isPublic &&
            !session.reveal,
        }"
      >
        <em>{{ nightOrder.get(player).other }}</em>
        <span class="description" v-if="player.role.otherNightReminder">
          <span v-html="player.role.otherNightReminder"></span>
        </span>
      </div>

      <div class="menu-container">
        <Token
          :role="player.role"
          :alignment="player.alignment"
          @click-token="clickToken()"
        />

        <transition name="fold">
          <ul class="menu" v-if="isMenuOpen && !session.reveal">
            <li @click="changeCharacter" v-if="!session.isSpectator">
              <font-awesome-icon icon="masks-theater" />Change Character
            </li>
            <li @click="changeAlignment" v-if="player.role.id">
              <font-awesome-icon icon="yin-yang" />Change Alignment
            </li>
            <li
              @click="changePronouns"
              v-if="session.isSpectator && player.id === session.playerId"
            >
              <font-awesome-icon icon="venus-mars" />Change Pronouns
            </li>
            <template v-if="!session.isSpectator">
              <li
                @click="swapPlayer()"
                :class="{ disabled: session.lockedVote }"
              >
                <font-awesome-icon icon="right-left" />
                Swap Seats
              </li>
              <li @click="changeName" v-if="!(player.id && session.sessionId)">
                <font-awesome-icon icon="user-pen" />Rename
              </li>
              <li
                @click="removePlayer"
                :class="{ disabled: session.lockedVote }"
              >
                <font-awesome-icon icon="circle-xmark" />
                Remove
              </li>
              <li
                @click="updatePlayer('id', '', true)"
                v-if="player.id && session.sessionId"
              >
                <font-awesome-icon icon="chair" />
                Empty Seat
              </li>
              <template
                v-if="
                  !session.nomination && !grimoire.isNight && !session.reveal
                "
              >
                <li @click="nominatePlayer()">
                  <font-awesome-icon icon="hand-point-right" />
                  Nominate Player
                </li>
              </template>
            </template>
            <li
              @click="claimSeat"
              v-if="session.isSpectator"
              :class="{ disabled: player.id && player.id !== session.playerId }"
            >
              <font-awesome-icon icon="chair" />
              <template v-if="!player.id"> Claim seat </template>
              <template v-else-if="player.id === session.playerId">
                Vacate Seat
              </template>
              <template v-else> Seat Occupied </template>
            </li>
          </ul>
        </transition>
      </div>

      <!-- Overlay icons -->
      <div class="overlay">
        <font-awesome-icon icon="hand" class="hand" />
        <font-awesome-icon
          icon="hand"
          class="vote"
          title="Hand UP"
          @click="vote()"
        />
        <font-awesome-icon
          icon="xmark"
          class="vote"
          title="Hand DOWN"
          @click="vote()"
        />
        <font-awesome-icon icon="question" class="vote" />
        <font-awesome-icon
          icon="circle-xmark"
          class="cancel"
          title="Cancel"
          @click="cancel()"
        />
        <font-awesome-icon
          icon="right-left"
          class="swap"
          @click="swapPlayer(player)"
          title="Swap seats with this player"
        />
        <font-awesome-icon
          icon="hand-point-right"
          class="nominate"
          @click="nominatePlayer(player)"
          title="Nominate this player"
        />
      </div>

      <!-- Claimed seat icon -->
      <font-awesome-icon
        icon="chair"
        v-if="player.id && session.sessionId"
        class="seat"
        :class="{ highlight: session.isRolesDistributed }"
      />

      <!-- Ghost vote icon -->
      <font-awesome-icon
        icon="check-to-slot"
        class="has-vote"
        v-if="player.isDead && !player.isVoteless"
        @click="updatePlayer('isVoteless', true)"
        title="Ghost vote"
      />

      <!-- On block icon -->
      <div class="marked">
        <font-awesome-icon icon="skull" />
      </div>
      <div
        class="name"
        @click="isMenuOpen = !isMenuOpen"
        :class="{ active: isMenuOpen && !session.reveal }"
      >
        <span>{{ player.name }}</span>
        <span v-if="player.pronouns">&emsp13;</span>
        <font-awesome-icon icon="venus-mars" v-if="player.pronouns" />
        <div class="pronouns" v-if="player.pronouns">
          <span>{{ player.pronouns }}</span>
        </div>
      </div>
    </div>

    <template v-if="player.reminders">
      <div
        class="reminder"
        :key="reminder.role + ' ' + reminder.name"
        v-for="reminder in player.reminders"
        :class="{
          custom: reminder.role === 'custom' && reminder.name.length > 15,
          flipped: reminder.flipped,
        }"
      >
        <span
          class="icon"
          :style="{
            backgroundImage: `url(${
              reminder.image && grimoire.isImageOptIn
                ? Array.isArray(reminder.image)
                  ? reminder.image[0]
                  : reminder.image
                : require(
                    '../assets/icons/' +
                      (reminder.imageAlt || reminder.role) +
                      (reminder.team === 'townsfolk' ||
                      reminder.team === 'outsider'
                        ? '_g'
                        : reminder.team === 'minion' ||
                            reminder.team === 'demon'
                          ? '_e'
                          : '') +
                      '.webp',
                  )
            })`,
          }"
        ></span>
        <svg
          viewBox="10 10 180 180"
          class="name"
          v-if="reminder.role !== 'custom' || reminder.name.length <= 15"
        >
          <path
            d="M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0"
            id="circle"
            fill="transparent"
            transform="rotate(48, 100, 100)"
          />
          <text width="150" dx="100%" text-anchor="middle" class="text">
            <textPath xlink:href="#circle">
              {{ reminder.name }}
            </textPath>
          </text>
        </svg>
        <span class="customText" v-else>{{ reminder.name }}</span>
        <div class="overlay">
          <font-awesome-icon
            @click="flipReminder(reminder)"
            icon="rotate-right"
            class="flip"
          />
          <font-awesome-icon
            @click="removeReminder(reminder)"
            icon="trash-can"
            class="remove"
          />
        </div>
      </div>
    </template>
    <div class="reminder add" @click="$emit('trigger', ['openReminderModal'])">
      <span class="icon"></span>
    </div>
    <div class="reminderHoverTarget"></div>
  </li>
</template>

<script>
import Token from "./Token";
import { mapGetters, mapState } from "vuex";

export default {
  components: {
    Token,
  },
  props: {
    player: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapState("players", ["players"]),
    ...mapState(["grimoire", "session"]),
    ...mapGetters({ nightOrder: "players/nightOrder" }),
    index: function () {
      return this.players.indexOf(this.player);
    },
    voteLocked: function () {
      const session = this.session;
      const players = this.players.length;
      if (!session.nomination) return false;
      const indexAdjusted =
        (this.index - 1 + players - session.nomination[1]) % players;
      return indexAdjusted < session.lockedVote - 1;
    },
    zoom: function () {
      if (this.players.length < 7) {
        return { width: 18 + this.grimoire.zoom + "vh" };
      } else if (this.players.length <= 10) {
        return { width: 16 + this.grimoire.zoom + "vh" };
      } else if (this.players.length <= 15) {
        return { width: 14 + this.grimoire.zoom + "vh" };
      } else {
        return { width: 12 + this.grimoire.zoom + "vh" };
      }
    },
  },
  data() {
    return {
      isMenuOpen: false,
      isSwap: false,
    };
  },
  methods: {
    changeCharacter() {
      if (this.session.reveal) return;
      this.$emit("trigger", ["openRoleModal"]);
      this.isMenuOpen = !this.isMenuOpen;
    },
    changeAlignment() {
      if (this.session.reveal) return;
      let newAlignment = "g";
      if (this.player.alignment === "g") {
        newAlignment = "e";
      } else if (this.player.alignment === "e") {
        newAlignment = this.player.role.team === "traveler" ? "n" : "g";
      }
      this.updatePlayer("alignment", newAlignment);
    },
    changePronouns() {
      if (this.session.isSpectator && this.player.id !== this.session.playerId)
        return;
      const pronouns = prompt("Player Pronouns", this.player.pronouns);
      //Only update pronouns if not null (prompt was not cancelled)
      if (pronouns !== null) {
        this.updatePlayer("pronouns", pronouns, true);
      }
    },
    clickToken() {
      if (this.session.reveal) {
        this.reveal();
      } else if (this.session.isSpectator) {
        this.$emit("trigger", ["openRoleModal"]);
      } else {
        this.isMenuOpen = !this.isMenuOpen;
      }
    },
    clickShroud() {
      if (this.grimoire.isNight) {
        if (this.player.isDead) return;
        this.updatePlayer("isInvisibleDead", !this.player.isInvisibleDead);
        if (this.player.isInvisibleDead) {
          this.$emit("trigger", ["openDeathReminderModal"]);
        }
      } else {
        this.updatePlayer("isDead", !this.player.isDead);
        if (this.player.isDead) {
          const reminders = [...this.player.reminders].filter(
            (reminder) => reminder.name !== "Dead",
          );
          this.updatePlayer("reminders", reminders, true);
        }
        if (this.session.markedPlayer === this.index) {
          this.$store.commit("session/setMarkedPlayer", -1);
        }
        if (this.player.isInvisibleDead) {
          this.updatePlayer("isInvisibleDead", false);
        }
        if (this.player.isVoteless) {
          this.updatePlayer("isVoteless", false);
        }
      }
    },
    reveal() {
      if (
        this.session.isSpectator ||
        !this.session.reveal ||
        this.session.revealedPlayers[this.index]
      )
        return;
      this.$store.commit("session/reveal", this.index);
      if (
        this.session.revealedPlayers.filter(Boolean).length >=
        this.players.length
      ) {
        setTimeout(() => {
          this.$store.commit("session/setReveal", {
            isRevealInProgress: false,
          });
        }, 3000);
      }
    },
    toggleStatus() {
      if (this.session.reveal) {
        this.reveal();
      } else if (!this.grimoire.isNight) {
        if (!this.player.isDead) {
          this.updatePlayer("isDead", true);
          if (this.session.markedPlayer === this.index) {
            this.$store.commit("session/setMarkedPlayer", -1);
          }
          if (this.player.isInvisibleDead) {
            this.updatePlayer("isInvisibleDead", false);
          }
        } else if (this.player.isVoteless) {
          this.updatePlayer("isVoteless", false);
          this.updatePlayer("isDead", false);
        } else {
          this.updatePlayer("isVoteless", true);
        }
      }
    },
    changeName() {
      if (this.session.isSpectator) return;
      const name = prompt("Player Name", this.player.name) || this.player.name;
      this.updatePlayer("name", name, true);
    },
    removeReminder(reminder) {
      const reminders = [...this.player.reminders];
      reminders.splice(this.player.reminders.indexOf(reminder), 1);
      this.updatePlayer("reminders", reminders, true);
    },
    flipReminder(reminder) {
      const reminders = [...this.player.reminders];
      reminder.flipped = !reminder.flipped;
      this.updatePlayer("reminders", reminders, true);
    },
    updatePlayer(property, value, closeMenu = false) {
      if (
        this.session.isSpectator &&
        property !== "reminders" &&
        property !== "pronouns" &&
        property !== "alignment"
      )
        return;
      this.$store.commit("players/update", {
        player: this.player,
        property,
        value,
      });
      if (closeMenu) {
        this.isMenuOpen = false;
      }
    },
    removePlayer() {
      if (this.session.reveal) return;
      this.isMenuOpen = false;
      this.$emit("trigger", ["removePlayer"]);
    },
    swapPlayer(player) {
      if (this.session.reveal) return;
      this.isMenuOpen = false;
      this.$emit("trigger", ["swapPlayer", player]);
    },
    nominatePlayer(player) {
      if (this.session.reveal) return;
      this.isMenuOpen = false;
      this.$emit("trigger", ["nominatePlayer", player]);
    },
    cancel() {
      this.$emit("trigger", ["cancel"]);
    },
    claimSeat() {
      this.isMenuOpen = false;
      this.$emit("trigger", ["claimSeat"]);
    },
    /**
     * Allow the ST to override a locked vote.
     */
    vote() {
      if (this.session.isSpectator || !this.voteLocked) return;
      this.$store.commit("session/voteSync", [
        this.index,
        !this.session.votes[this.index],
      ]);
    },
  },
};
</script>

