    pointer-events: none;
    transition: all 250ms;
    transform: scale(0.2);
    * {
      stroke-width: 10px;
      stroke: black;
      fill: url(#gold-overlay);
    }
    &:hover * {
      fill: url(#default-overlay);
    }
    &.fa-hand * {
      fill: url(#gold-overlay);
    }
    &.hand.fa-hand {
      width: 60%;
      height: 72%;
      transform: none;
      transform-origin: center 150%;
      transition: all 350ms;
    }
    &.fa-xmark * {
      fill: url(#purple-overlay);
    }
    &.fa-question * {
      fill: url(#default-overlay);
    }
  }
}

.player.hand-raised .overlay svg.hand.fa-hand {
  opacity: 1;
  transform: rotateZ(45deg) translateY(-25%);
}

#townsquare.reveal .player .overlay svg.hand.fa-hand,
#townsquare.voteInProgress .player .overlay svg.hand.fa-hand {
  opacity: 0;
}

// other player voted yes, but is not locked yet
#townsquare.vote .player.vote-yes .overlay svg.vote.fa-hand {
  opacity: 0.75;
  transform: scale(0.85);
}

// you are st, a locked vote yes | you are st, a locked vote no
#townsquare.vote:not(.spectator)
  .player.vote-lock.vote-yes
  .overlay
  svg.vote.fa-hand,
#townsquare.vote:not(.spectator)
  .player.vote-lock:not(.vote-yes)
  .overlay
  svg.vote.fa-xmark {
  opacity: 1;
  transform: scale(1);
  pointer-events: all;
}

// you are st, vote Watching disabled, an unlocked vote yes | you are st, vote Watching disabled, a vote no
#townsquare.vote:not(.spectator):not(.voteWatching)
  .player:not(.vote-lock)
  .overlay
  svg.vote.fa-hand
  *,
#townsquare.vote:not(.spectator):not(.voteWatching)
  .player
  .overlay
  svg.vote.fa-xmark
  * {
  fill: url(#default-overlay);
}

// you voted yes | a locked vote yes | Vote Watching enabled, a locked vote no | Vote Watching disabled, your locked vote no
#townsquare.vote .player.you.vote-yes .overlay svg.vote.fa-hand,
#townsquare.vote .player.vote-lock.vote-yes .overlay svg.vote.fa-hand,
#townsquare.vote.voteWatching
  .player.vote-lock:not(.vote-yes)
  .overlay
  svg.vote.fa-xmark,
#townsquare.vote:not(voteWatching)
  .player.you.vote-lock:not(.vote-yes)
  .overlay
  svg.vote.fa-xmark {
  opacity: 1;
  transform: scale(1);
}

// Vote Watching disabled, a locked vote
#townsquare.vote.spectator:not(.voteWatching)
  .player:not(.you).vote-lock
  .overlay
  svg.vote.fa-question {
  opacity: 1;
  transform: scale(0.8);
}

li.from:not(.nominate) .player .overlay svg.cancel {
  opacity: 1;
  transform: scale(1);
  pointer-events: all;
}

li.swap:not(.from) .player .overlay svg.swap,
li.nominate .player .overlay svg.nominate {
  opacity: 1;
  transform: scale(1);
  pointer-events: all;
}

