/****** Session seat glow *****/
@mixin glow($name, $color) {
  @keyframes #{$name}-glow {
    0% {
      box-shadow: 0 0 rgba($color, 1);
      border-color: $color;
    }
    50% {
      border-color: black;
    }
    100% {
      box-shadow: 0 0 20px 16px transparent;
      border-color: $color;
    }
  }

  .player.you.#{$name} .token {
    animation: #{$name}-glow 5s ease-in-out infinite;
  }
}

@include glow("townsfolk", $townsfolk);
@include glow("outsider", $outsider);
@include glow("minion", $minion);
@include glow("demon", $demon);
@include glow("traveler", $traveler);
@include glow("none", #f4c560);

@mixin glow($name, $alignment, $color) {
  @keyframes #{$name + $alignment}-glow {
    0% {
      box-shadow: 0 0 rgba($color, 1);
      border-color: $color;
    }
    50% {
      border-color: black;
    }
    100% {
      box-shadow: 0 0 20px 16px transparent;
      border-color: $color;
    }
  }

  .player.you.#{$name}.#{$alignment} .token {
    animation: #{$name + $alignment}-glow 5s ease-in-out infinite;
  }
}

@include glow("townsfolk", "e", $minion);
@include glow("outsider", "e", $minion);
@include glow("minion", "g", $outsider);
@include glow("demon", "g", $outsider);

.player.you .token {
  animation: none-glow 5s ease-in-out infinite;
}

