const NEWPLAYER = {
  name: "",
  id: "",
  role: {},
  reminders: [],
  isVoteless: false,
  hasResponded: {},
  isDead: false,
  isInvisibleDead: false,
  pronouns: "",
  alignment: "",
};

const NEWSTORYTELLER = {
  name: "Storyteller",
  role: {},
  reminders: [],
  pronouns: "",
};

const state = () => ({
  players: [],
  storyteller: NEWSTORYTELLER,
  fabled: [],
  bluffs: [{}, {}, {}, {}, {}, {}],
  extraRoles: [],
});

const getters = {
  alive({ players }) {
    return players.filter((player) => !player.isDead).length;
  },
  nonTravelers({ players }) {
    const nonTravelers = players.filter(
      (player) => player.role.team !== "traveler",
    );
    return Math.min(nonTravelers.length, 15);
  },
  // calculate a Map of player => night order
  nightOrder({ players, storyteller, extraRoles, fabled }, getters, rootState) {
    const firstNight = [0];
    const otherNight = [0];
    players
      .map((player) => player.role)
      .concat([storyteller.role], extraRoles, fabled)
      .forEach((role) => {
        role.firstNightInEdition = rootState.edition.firstNight
          ? rootState.edition.firstNight.includes(role.id)
            ? rootState.edition.firstNight.indexOf(role.id) + 2
            : 0
          : role.firstNight;
        role.otherNightInEdition = rootState.edition.otherNight
          ? rootState.edition.otherNight.includes(role.id)
            ? rootState.edition.otherNight.indexOf(role.id) + 2
            : 0
          : role.otherNight;
        if (
          role.firstNightInEdition > 0 &&
          !firstNight.includes(role.firstNightInEdition)
        ) {
          firstNight.push(role.firstNightInEdition);
        }
        if (
          role.otherNightInEdition > 0 &&
          !otherNight.includes(role.otherNightInEdition)
        ) {
          otherNight.push(role.otherNightInEdition);
        }
      });
    firstNight.sort((a, b) => a - b);
    otherNight.sort((a, b) => a - b);
    const nightOrder = new Map();
    players.concat([storyteller]).forEach((player) => {
      const first = Math.max(
        firstNight.indexOf(player.role.firstNightInEdition),
        0,
      );
      const other = Math.max(
        otherNight.indexOf(player.role.otherNightInEdition),
        0,
      );
      nightOrder.set(player, { first, other });
    });
    extraRoles.concat(fabled).forEach((role) => {
      const first = Math.max(firstNight.indexOf(role.firstNightInEdition), 0);
      const other = Math.max(otherNight.indexOf(role.otherNightInEdition), 0);
      nightOrder.set(role, { first, other });
    });
    return nightOrder;
  },
};

const actions = {
  randomize({ state, commit }) {
    const players = state.players
      .map((a) => [Math.random(), a])
      .sort((a, b) => a[0] - b[0])
      .map((a) => a[1]);
    commit("set", players);
  },
  clearRoles({ state, commit, rootState }) {
    let players;
    if (rootState.session.isSpectator) {
      players = state.players.map((player) => {
        if (player.role.team !== "traveler") {
          player.role = {};
        }
        player.reminders = [];
        return player;
      });
    } else {
      players = state.players.map(({ name, id, pronouns }) => ({
        ...NEWPLAYER,
        name,
        id,
        pronouns,
      }));
    }
    const name = state.storyteller.name;
    const pronouns = state.storyteller.pronouns;
    commit("set", players);
    commit("setStoryteller", {
      ...NEWSTORYTELLER,
      name,
      pronouns,
    });
    commit("setBluff");
    commit("setExtraRoles");
  },
};

const mutations = {
  clear(state) {
    state.players = [];
    state.storyteller = NEWSTORYTELLER;
    state.bluffs = [{}, {}, {}, {}, {}, {}];
    state.extraRoles = [];
    state.fabled = [];
  },
  set(state, players = []) {
    state.players = players;
  },
  setStoryteller(state, storyteller) {
    state.storyteller = storyteller;
  },
  /**
  The update mutation also has a property for isFromSockets
  this property can be added to payload object for any mutations
  then can be used to prevent infinite loops when a property is
  able to be set from multiple different session on websockets.
  An example of this is in the sendPlayerPronouns and _updatePlayerPronouns
  in socket.js.
   */
  update(state, { player, property, value }) {
    const index = state.players.indexOf(player);
    if (index >= 0) {
      state.players[index][property] = value;
      if (property === "role" && value) {
        state.players[index].alignment =
          value.team === "townsfolk" || value.team === "outsider"
            ? "g"
            : value.team === "minion" || value.team === "demon"
              ? "e"
              : "n";
      }
    }
  },
  updateStoryteller(state, { property, value }) {
    state.storyteller[property] = value;
  },
  add(state, name) {
    state.players.push({
      ...NEWPLAYER,
      name,
    });
  },
  remove(state, index) {
    state.players.splice(index, 1);
  },
  swap(state, [from, to]) {
    [state.players[from], state.players[to]] = [
      state.players[to],
      state.players[from],
    ];
    // hack: "modify" the array so that Vue notices something changed
    state.players.splice(0, 0);
  },
  setBluff(state, { index, role } = {}) {
    if (index !== undefined) {
      state.bluffs.splice(index, 1, role);
    } else {
      state.bluffs = [{}, {}, {}, {}, {}, {}];
    }
  },
  setExtraRoles(state, { index, role } = {}) {
    if (index !== undefined) {
      state.extraRoles.splice(index, 1);
    } else if (role) {
      if (!Array.isArray(role)) {
        state.extraRoles.push(role);
      } else {
        state.extraRoles = role;
      }
    } else {
      state.extraRoles.splice(0, state.extraRoles.length);
    }
  },
  setFabled(state, { index, fabled } = {}) {
    if (index !== undefined) {
      state.fabled.splice(index, 1);
    } else if (fabled) {
      if (!Array.isArray(fabled)) {
        state.fabled.push(fabled);
      } else {
        state.fabled = fabled;
      }
    } else {
      state.fabled.splice(0, state.fabled.length);
    }
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
