<template>
  <div
    id="townsquare"
    class="square"
    :class="{
      public: grimoire.isPublic,
      reveal: session.reveal,
      night: grimoire.isNight,
      spectator: session.isSpectator,
      host: session.sessionId && !session.isSpectator,
      spin: session.isTokenSpinning,
      vote: session.nomination,
      voteInProgress: session.isVoteInProgress,
      voteWatching: session.isVoteWatchingAllowed,
    }"
  >
    <ul class="circle" :class="['size-' + players.length]">
      <Player
        v-for="(player, index) in players"
        :key="index"
        :player="player"
        @trigger="handleTrigger(index, $event)"
        :class="{
          from: Math.max(swap, nominate) === index,
          swap: swap > -1,
          nominate: nominate > -1,
        }"
      ></Player>
    </ul>

    <div class="top-left" v-if="players.length">
      <Storyteller
        :key="-1"
        :storyteller="storyteller"
        @trigger="handleTrigger(20, $event)"
      ></Storyteller>
      <div
        class="fabled"
        :class="{ closed: !grimoire.isFabledOpen }"
        v-if="fabled.length"
      >
        <ul>
          <li
            v-for="(role, index) in fabled"
            :key="index"
            @click="removeFabled(index)"
          >
            <div
              class="night-order first"
              :class="{
                shown:
                  nightOrder.get(role).first &&
                  grimoire.isNightOrder &&
                  !session.reveal,
              }"
            >
              <em>{{ nightOrder.get(role).first }}</em>
              <span class="description" v-if="role.firstNightReminder">
                <span v-html="role.firstNightReminder"></span>
              </span>
            </div>
            <div
              class="night-order other"
              :class="{
                shown:
                  nightOrder.get(role).other &&
                  grimoire.isNightOrder &&
                  !session.reveal,
              }"
            >
              <em>{{ nightOrder.get(role).other }}</em>
              <span class="description" v-if="role.otherNightReminder">
                <span v-html="role.otherNightReminder"></span>
              </span>
            </div>
            <Token :role="role"></Token>
          </li>
        </ul>
      </div>
    </div>

    <div
      class="bluffs"
      v-if="players.length"
      ref="bluffs"
      :class="{
        closed: !grimoire.isBluffsOpen,
        double: grimoire.isExtraBluffs,
      }"
    >
      <ul>
        <li v-for="index1 in bluffSets" :key="index1">
          <ul>
            <li
              v-for="index2 in 3"
              :key="index2"
              @click="openRoleModal((3 * (index1 - 1) + index2) * -1)"
            >
              <Token :role="bluffs[3 * (index1 - 1) + index2 - 1]"></Token>
            </li>
          </ul>
        </li>
      </ul>
    </div>

    <div
      class="extra-roles"
      v-if="players.length && extraRoles.length"
      ref="extra-roles"
    >
      <ul>
        <li
          v-for="(role, index) in extraRoles"
          :key="index"
          @click="removeExtraRoles(index)"
        >
          <div
            class="night-order first"
            :class="{
              shown:
                nightOrder.get(role).first &&
                grimoire.isNightOrder &&
                !session.reveal,
            }"
          >
            <em>{{ nightOrder.get(role).first }}</em>
            <span class="description" v-if="role.firstNightReminder">
              <span v-html="role.firstNightReminder"></span>
            </span>
          </div>
          <div
            class="night-order other"
            :class="{
              shown:
                nightOrder.get(role).other &&
                grimoire.isNightOrder &&
                !session.reveal,
            }"
          >
            <em>{{ nightOrder.get(role).other }}</em>
            <span class="description" v-if="role.otherNightReminder">
              <span v-html="role.otherNightReminder"></span>
            </span>
          </div>
          <Token :role="role"></Token>
        </li>
      </ul>
    </div>

    <ReminderModal
      :player-index="selectedPlayer"
      :is-death="deathReminder"
    ></ReminderModal>
    <RoleModal :player-index="selectedPlayer"></RoleModal>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import Player from "./Player";
import Storyteller from "./Storyteller";
import Token from "./Token";
import ReminderModal from "./modals/ReminderModal";
import RoleModal from "./modals/RoleModal";

export default {
  components: {
    Player,
    Storyteller,
    Token,
    RoleModal,
    ReminderModal,
  },
  computed: {
    ...mapGetters({ nightOrder: "players/nightOrder" }),
    ...mapState(["grimoire", "roles", "session"]),
    ...mapState("players", [
      "players",
      "storyteller",
      "bluffs",
      "extraRoles",
      "fabled",
    ]),
    bluffSets() {
      return this.grimoire.isExtraBluffs ? 2 : 1;
    },
  },
  data() {
    return {
      selectedPlayer: 0,
      deathReminder: false,
      swap: -1,
      nominate: -1,
    };
  },
  methods: {
    removeExtraRoles(index) {
      this.$store.commit("players/setExtraRoles", { index });
    },
    removeFabled(index) {
      if (this.session.isSpectator) return;
      this.$store.commit("players/setFabled", { index });
      this.$store.commit("toggleFabled", true);
    },
    handleTrigger(playerIndex, [method, params]) {
      if (typeof this[method] === "function") {
        this[method](playerIndex, params);
      }
    },
    claimSeat(playerIndex) {
      if (!this.session.isSpectator) return;
      if (this.session.playerId === this.players[playerIndex].id) {
        this.$store.commit("session/claimSeat", -1);
      } else {
        this.$store.commit("session/claimSeat", playerIndex);
      }
    },
    openReminderModal(playerIndex) {
      this.selectedPlayer = playerIndex;
      this.deathReminder = false;
      this.$store.commit("toggleModal", "reminder");
    },
    openDeathReminderModal(playerIndex) {
      this.selectedPlayer = playerIndex;
      this.deathReminder = true;
      this.$store.commit("toggleModal", "reminder");
    },
    openRoleModal(playerIndex) {
      const player = this.players[playerIndex];
      if (this.session.isSpectator && player && player.role.team === "traveler")
        return;
      this.selectedPlayer = playerIndex;
      this.$store.commit("toggleModal", "role");
    },
    removePlayer(playerIndex) {
      if (this.session.isSpectator || this.session.lockedVote) return;
      if (
        confirm(
          `Do you really want to remove ${this.players[playerIndex].name}?`,
        )
      ) {
        const { nomination } = this.session;
        if (nomination) {
          if (nomination.includes(playerIndex)) {
            // abort vote if removed player is either nominator or nominee
            this.$store.commit("session/nomination");
          } else if (
            nomination[0] > playerIndex ||
            nomination[1] > playerIndex
          ) {
            // update nomination array if removed player has lower index
            this.$store.commit("session/setNomination", [
              nomination[0] > playerIndex ? nomination[0] - 1 : nomination[0],
              nomination[1] > playerIndex ? nomination[1] - 1 : nomination[1],
            ]);
          }
        }
        this.$store.commit("players/remove", playerIndex);
      }
    },
    swapPlayer(from, to) {
      if (this.session.isSpectator || this.session.lockedVote) return;
      if (to === undefined) {
        this.cancel();
        this.swap = from;
      } else {
        if (this.session.nomination) {
          // update nomination if one of the involved players is swapped
          const swapTo = this.players.indexOf(to);
          const updatedNomination = this.session.nomination.map((nom) => {
            if (nom === this.swap) return swapTo;
            if (nom === swapTo) return this.swap;
            return nom;
          });
          if (
            this.session.nomination[0] !== updatedNomination[0] ||
            this.session.nomination[1] !== updatedNomination[1]
          ) {
            this.$store.commit("session/setNomination", updatedNomination);
          }
        }
        this.$store.commit("players/swap", [
          this.swap,
          this.players.indexOf(to),
        ]);
        this.cancel();
      }
    },
    nominatePlayer(from, to) {
      if (this.session.isSpectator || this.session.lockedVote) return;
      if (to === undefined) {
        this.cancel();
        if (from !== this.nominate) {
          this.nominate = from;
        }
      } else {
        const nomination = [this.nominate, this.players.indexOf(to)];
        this.$store.commit("session/nomination", { nomination });
        this.cancel();
      }
    },
    cancel() {
      this.swap = -1;
      this.nominate = -1;
    },
  },
};
</script>

<style lang="scss">
@use "sass:math";
@import "../vars.scss";

#townsquare {
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.circle {
  padding: 0;
  width: 100%;
  height: 100%;
  list-style: none;
  margin: 0;

  > li {
    position: absolute;
    left: 50%;
    height: 50%;
    transform-origin: 0 100%;
    pointer-events: none;

    &:hover {
      z-index: 25 !important;
    }

    &:has(.player.menu-open) {
      z-index: 26 !important;
    }

    > .player {
      margin-left: -50%;
      width: 100%;
      pointer-events: all;
    }
    > .reminder {
      margin-left: calc(-25% - 1px);
      width: 50%;
      pointer-events: all;
    }
  }
}

.top-left {
  position: absolute;
  left: 12.25px;
  top: 12.25px;
  display: flex;
  list-style: none;
  > li {
    > .reminder {
      margin-left: calc(25% - 1px);
      width: 50%;
      pointer-events: all;
    }
  }
}

@mixin on-circle($item-count) {
  $angle: math.div(360, $item-count);
  $rot: 0;

  // rotation and tooltip placement
  @for $i from 1 through $item-count {
    &:nth-child(#{$i}) {
      transform: rotate($rot * 1deg);
      @if $i <= math.ceil(math.div($item-count, 2)) {
        // first half of players
        z-index: $item-count - $i + 1;
        // open menu on the right
        .player > .menu-container > .menu {
          right: auto;
          left: 110%;
          margin-left: 15px;
          &:before {
            border-right-color: black;
            border-left-color: transparent;
            left: auto;
            right: 100%;
            margin-right: 1px;
          }
        }
        .player .overlay svg.hand.fa-hand {
          transform: scaleX(-100%);
        }
        .player.hand-raised .overlay svg.hand.fa-hand {
          transform: scaleX(-100%) rotateZ(45deg) translateY(-25%);
        }
        .fold-enter-active,
        .fold-leave-active {
          transform-origin: left center;
        }
        .fold-enter,
        .fold-leave-to {
          transform: perspective(200px) rotateY(90deg);
        }
        // show ability tooltip on the left
        .ability {
          right: 120%;
          left: auto;
          &:before {
            border-right-color: transparent;
            border-left-color: black;
            right: auto;
            left: 100%;
            margin-left: 2px;
          }
        }
        .pronouns {
          left: 110%;
          right: auto;
          &:before {
            border-left-color: transparent;
            border-right-color: black;
            left: auto;
            right: 100%;
            margin-right: 1px;
          }
        }
      } @else {
        // second half of players
        z-index: $i - 1;
      }

      > * {
        transform: rotate($rot * -1deg);
        &.reminder.flipped {
          transform: rotate($rot * -1deg) rotate(180deg);
          & .overlay {
            transform: rotate(-180deg);
          }
        }
      }

      // animation cascade
      .life,
      .token,
      .shroud,
      .night-order em,
      .seat {
        animation-delay: ($i - 1) * 50ms;
        transition-delay: ($i - 1) * 50ms;
      }

      // move reminders closer to the sides of the circle
      $q: math.div($item-count, 4);
      $x: $i - 1;
      @if $x < $q or ($x >= math.div($item-count, 2) and $x < $q * 3) {
        .player {
          margin-bottom: -10% + 20% * (1 - math.div($x % $q, $q));
        }
      } @else {
        .player {
          margin-bottom: -10% + 20% * math.div($x % $q, $q);
        }
      }
    }
    $rot: $rot + $angle;
  }
}

@mixin reveal-circle($item-count) {
  @for $i from 1 through $item-count {
    &:nth-child(#{$i}) {
      $duration: 125 * $item-count + 500;
      $startFlipPercentage: calc(50 * ($i - 1) / ($item-count + 3)) + "%";
      $stopFlipPercentage: calc(50 * ($i + 3) / ($item-count + 3)) + "%";
      $startFlash: calc(50 * ($i - 1) / ($item-count + 3) + 50);
      $stopFlash: calc(50 * ($i + 3) / ($item-count + 3) + 50);
      $startFlashPercentage: $startFlash + "%";
      $maxFlashPercentage: calc((7 * $startFlash + $stopFlash) / 8) + "%";
      $stopFlashPercentage: $stopFlash + "%";
      .token {
        animation: beginRevealToken- +
          $item-count +
          "-" +
          $i
          $duration +
          ms
          ease-in-out;

        @keyframes beginRevealToken-#{$item-count}-#{$i} {
          0%,
          #{$startFlipPercentage} {
            transform: perspective(400px) rotateY(0deg);
            pointer-events: none;
          }
          #{$stopFlipPercentage},
          100% {
            transform: perspective(400px) rotateY(-180deg);
            pointer-events: none;
          }
        }
      }
      .life {
        animation: beginRevealLife- +
          $item-count +
          "-" +
          $i
          $duration +
          ms
          ease-in-out;

        @keyframes beginRevealLife-#{$item-count}-#{$i} {
          0%,
          #{$startFlipPercentage} {
            transform: perspective(400px) rotateY(180deg);
            pointer-events: none;
          }
          #{$stopFlipPercentage} {
            transform: perspective(400px) rotateY(0deg);
          }
          #{$startFlashPercentage} {
            filter: brightness(0.45);
          }
          #{$maxFlashPercentage} {
            filter: brightness(0.9);
          }
          #{$stopFlashPercentage} {
            filter: brightness(0.45);
          }
          100% {
            transform: perspective(400px) rotateY(0deg);
            pointer-events: none;
          }
        }
      }
    }
  }
}

@for $i from 1 through 20 {
  .circle.size-#{$i} > li {
    @include on-circle($item-count: $i);
  }
  #townsquare.reveal > .circle.size-#{$i} > li {
    @include reveal-circle($item-count: $i);
  }
}

